.promo {
  padding: 120px 0 121px;
}

.promo__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.promo__inner {
  position: relative;
  display: flex;
  padding-right: 54%;
  min-height: 301px;
  flex-direction: column;
  justify-content: center;

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    width: 48.275%;
    min-height: 301px;
    background-color: $promo-bg;
    background-image: url("../img/promo-bg-desktop@2x.jpg");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;

    @media (max-resolution: $retina) {
      background-image: url("../img/promo-bg-desktop@1x.jpg");
    }
  }

  h2 {
    @include title;

    margin-bottom: 25px;
  }

  p {
    margin: 0;
  }
}

