.promo__linebreak {
  display: none;
}

@media (max-width: $desktop) {
  .promo {
    padding: 91px 0;
  }

  .promo__inner {
    padding-right: 52.5%;
    min-height: 264px;

    &::before {
      top: 9px;
      width: 47.787%;
      min-height: 264px;
      background-image: url("../img/promo-bg-tablet@2x.jpg");

      @media (max-resolution: $retina) {
        background-image: url("../img/promo-bg-tablet@1x.jpg");
      }
    }

    h2 {
      margin-bottom: 18px;
      max-width: 70%;
      font-size: rem(26);
      line-height: 36px;
    }
  }

  // .promo__img-wrap {
  //   width: 47.787%;
  //   min-height: 264px;
  //   background-image: url("../img/promo-bg-tablet@2x.jpg");

  //   @media (max-resolution: $retina) {
  //     background-image: url("../img/promo-bg-tablet@1x.jpg");
  //   }
  // }

  .promo__linebreak {
    display: block;
  }
}

@media (max-width: $tablet) {
  .promo {
    padding: 57px 0 65px;
  }

  .promo__container {
    flex-direction: column;
  }

  .promo__inner {
    justify-content: flex-start;
    padding-right: 0;
    max-width: 100%;
    min-height: 518px;

    &::before {
      top: auto;
      bottom: 0;
      width: 100%;
      min-height: 186px;
      background-image: url("../img/promo-bg-mobile@2x.jpg");

      @media (max-resolution: $retina) {
        background-image: url("../img/promo-bg-mobile@1x.jpg");
      }
    }

    h2 {
      margin-bottom: 15px;
      font-size: rem(24);
      line-height: 34px;
    }

    p {
      margin-bottom: 33px;
    }
  }

  // .promo__img-wrap {
